.navbar {
  height: 77px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #535353;
  margin: 0 auto;
  max-width: 1110px;
  width: 88%;
  position: relative;

  .mobile-menu-btn {
    width: 20px;
    height: 20px;
    display: none;
    cursor: pointer;
    z-index: 1;
    @media (max-width: 1000px) {
      display: flex;
    }
  }

  .nav-links {
    display: flex;
    @media (max-width: 1000px) {
      display: none;
    }
      a {
    margin-left: 40px;
    @media (max-width: 1000px) {
      margin-right: auto;
    }
    @media (max-width: 700px) {
      margin: 0 auto;
    }
  }
    .link-item {
      list-style-type: none;
      margin: 0 10px;
      letter-spacing: 2px;
    }

    .link-item a {
      text-decoration: none;
      color: #fff;
      font-size: 13px;
    }

    .link-item a:hover {
      color: #d87d4a;
    }
  }

  .cart-icon {
    cursor: pointer;
    background-image: url("/assets/shared/desktop/icon-cart.svg");
    background-repeat: no-repeat;
    width: 25px;
    height: 25px;
    color: #fff;
    position: relative;
    z-index: 1;
    .item-counter {
      padding: 2px 0 0 0;
      width: 20px;
      height: 20px;
      font-size: 12px;
      align-items: center;
      text-align: center;
      background-color: #d87d4a;
      border-radius: 100%;
      align-items: center;
      position: relative;
      left: 10px;
      top: 10px;
    }
  }
}

.mobile-menu {
  position: absolute;
  background-color: #fff;
  width: 100%;
  z-index: 4;
}
